import React, {Component} from 'react';

const resStyle = {position : 'fixed', top : '0px', right : '10px', height : '18px', outline : 'none', fontVariant : 'small-caps', backgroundColor : '#D3D3D3', color : 'white', border : 'none', borderBottomLeftRadius: 5, borderBottomRightRadius: 5, width : '20px', cursor : 'pointer', zIndex : '100', paddingBottom : '2', boxShadow : '0px 1px 3px #ccc'};

class PracticeMeter extends Component {
   constructor() {
      super();
      this.state = {};
      this.clearMeter = this.clearMeter.bind(this);
   }
   clearMeter() {
      if (window.confirm("Reset the Practice Meter?")) {
      localStorage.removeItem('cotfMeter');
      localStorage.removeItem('firstTested_cotf');
      localStorage.removeItem('lastTested_cotf');
      window.location.reload();
      }
   }
   render() {
      let barBgrndCol;
      if (this.props.meterlevel <= 20) barBgrndCol = 'rgba(255, 0, 0, 0.5)';
      else if (this.props.meterlevel > 20 && this.props.meterlevel < 80) barBgrndCol='rgba(255, 255, 0, 0.5)';
      else if (this.props.meterlevel >= 80) barBgrndCol = 'rgba(0, 255, 0, 0.5)';

      return (
      <div>
      <div id='pmeter'>
      <div id='pmeterBar' style={{width : `${this.props.meterlevel}%`, backgroundColor : barBgrndCol}}></div>
      <button id='resetMeter' onClick={this.clearMeter} style={resStyle}>×</button>
      </div>
      </div>
      );
   }
}

export default PracticeMeter;
